.title {
  font-weight: 900;

  @include xs-block {
    font-size: 48px;
    text-align: center;
    margin-bottom: rem(32);
    letter-spacing: -1.392px;
    margin-top: 120px;
  }
  @include sm-block {
    word-wrap: wrap;
    font-size: 58px;
    margin-bottom: rem(60);
  }
  @include lg-block {
    text-align: left;
    font-size: rem(62);
    margin-top: 0;
  }
  @include xl-block {
    font-size: rem(82);
    font-weight: 900;
