.text {
  color: #9297a2;

  @include xs-block {
    text-align: center;
    font-size: rem(22);
    margin-bottom: rem(30);
  }
  @include sm-block {
    font-size: rem(28);
  }
  @include lg-block {
    font-size: rem(24);
    text-align: left;
  }
  @include xl-block {
