.section_link {
  font-weight: bold;
  font-size: rem(28);

  a {
    background: no-repeat url('../img/svg/Arr_for_link.svg');
    background-position: rem(10) rem(0);
    background-size: rem(20);
    padding-left: rem(40);
  }

  @include xs-block {
    width: 90%;
    text-align: center;
  }
  @include sm-block {
    width: 100%;
    text-align: center;
  }
  @include md-block {
    width: 95%;

    text-align: center;
  }
  @include lg-block {
    position: absolute;
    display: flex;
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;
  }
  @include xl-block {
    position: absolute;
    display: flex;
    margin-top: 0;
    width: 100%;
  }
}

.dots {
  @include xs-block {
    display: none;
  }
  @include lg-block {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    display: block;
    z-index: 99;
    position: fixed;
    height: rem(100);
    width: rem(40);
    top: 50%;
    right: 5%;

    a {
      margin: rem(5) 0;
      cursor: pointer;
      display: block;
      padding: rem(10) rem(15);
      width: rem(20);
      height: rem(20);

      span {
        border-radius: 100%;
        display: block;
        width: 4px;
        height: 4px;
        background: #0072ff;
        transition: all 0.3s ease-in-out;
        @media screen and (min-width: 3940px) {
          width: 8px;
          height: 8px;
        }
      }
    }
  }

  @include xl-block {
    right: 3vw;
  }
}

.dot_check {
  @include lg-block {
    transform: scale(2.8);
    opacity: 0.7;
    transition: all 0.3s ease-in-out;
  }
}

.downloads_icons {
  @include xs-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 147px;
    align-self: flex-start;
    margin-bottom: 20px;
    img {
      width: 200px;
