    margin-bottom: 0;
    height: 60px;
    img {
      width: auto;
      height: 70;
    }
  }

  @include lg-block {
    width: rem(362);
    height: rem(48);
    justify-content: space-between;
    margin: 0;
    align-self: flex-start;
    img {
      width: auto;
      height: rem(48);
    }
  }

  @include xl-block {
    width: rem(442);
    justify-content: space-between;
    margin: 0;
    img {
      width: auto;
      height: rem(58);
    }
  }
}

