.how_it_works {
  color: #fff;
  position: relative;
  @include xs-block {
    height: 1196px;
  }
  @include sm-block {
    height: 1160px;
  }
  @include md-block {
    height: 1110px;
  }
  @include lg-block {
    height: 101vh;
  }

  .header {
    @include xs-block {
      display: none;
    }
    @include lg-block {
      display: flex;
    }
  }

  .content {
    display: flex;
    position: relative;

    @include xs-block {
      flex-direction: column;
      flex-wrap: wrap;
    }
    @include lg-block {
      flex-direction: row;
      justify-content: space-between;
      margin-top: rem($mr_from_header);
    }

    .description {
      @include description;
      @include size-xs(12);
      @include size-lg(6);

      @include xs-block {
        display: flex;
        flex-direction: column;
      }
      @include xl-block {
        justify-content: center;
      }

      .how_it_works__title {
        margin-bottom: 0;

        @include sm-block {
          margin-top: 80px;
        }
        @include lg-block {
          margin-top: 0;
        }
      }

      .how_it_works__text {
        margin-bottom: 0;
      }
    }

    .viwe {
      box-sizing: border-box;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      align-items: center;

      @include xs-block {
        position: relative;
        width: 100%;
        overflow: visible;
        left: 0;
        border: none;
        padding-top: 30px;
      }
      @include size-lg(5);
      @include size-xl(6);

      .featches {
        padding-top: 0;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include xs-block {
          align-items: flex-end;
          width: 315px;
        }
        @include sm-block {
          align-items: center;
        }

        @include size-sm(12);
        @include lg-block {
          width: rem(300);
        }
        @include size-xl(12);

        .feache__item {
          display: flex;
          align-items: center;
          box-sizing: border-box;
          color: #000;
          margin-bottom: 4.9vh;
          position: relative;
          border-radius: rem(28);

          @include xs-block {
            width: 100%;
            padding: 25px 10px 25px 100px;
            margin-bottom: 55px;
            margin-left: 16px;
          }
          @include lg-block {
            height: rem(100);
            padding: rem(15) 0 rem(25) rem(100);
            margin-bottom: rem(40);
            opacity: 0;
          }
          @include size-xl(12);
          @include xl-block {
            padding: rem(25) 0 rem(25) rem(100);
            opacity: 0;
          }

          &:nth-child(1) {
            background: no-repeat url('../img/svg/ic.svg'), #ffffff;
            @include feache_bg();
            &::before {
              content: '1';
              @include feach_icon;
            }
          }
          &:nth-child(2) {
            background: no-repeat url('../img/svg/ic2.svg'), #ffffff;
            @include feache_bg();

            &::before {
              content: '2';
              @include feach_icon;
            }
          }
          &:nth-child(3) {
            background: no-repeat url('../img/svg/ic3.svg'), #ffffff;
            @include feache_bg();

            &::before {
              content: '3';
              @include feach_icon;
            }
          }

          h4 {
            font-size: rem(20);
          }
        }
      }
    }
  }

  .section_link {
    @include xs-block {
      width: 170px;
      position: absolute;
      left: calc(50% - 90px);
      padding-left: 0;
      background-position: 30px 5px;
      bottom: 70px;
    }

    @include sm-block {
      width: 220px;
      margin-top: 0;
      bottom: 50px;
      left: calc(50% - 110px);
    }
    @include md-block {
      bottom: 90px;
      width: rem(170);
    }
    @include lg-block {
      width: rem(170);
      left: 0;
      bottom: rem(60);
    }

    .how_it_works__selection_link {
      color: #fff;
      background: no-repeat url('../img/svg/Arr_for_link_white.svg');
      background-position: rem(10) rem(0);
      background-size: rem(20);
      padding-left: rem(40);

      @include xs-block {
        display: inline-block;
        width: 100%;
        background-position: rem(13);
      }
    }
  }
}

#download {
  $space_y: 320;
  position: relative;
  @include lg-block {
    height: 100vh;
    background: no-repeat url('../img/svg/main_bg.svg');
    background-position-y: -22vh;
    background-size: rem(1400);
  }
  @include xl-block {
    background-size: rem(1700);
    background-position-y: rem(-$space_y);
  }

  @for $i from 0 through 4 {
    $space_x_800: 150px;
    $width_992: 992px;
    @media screen and (max-height: 800px) and (min-width: $width_992 + $i * 50) {
      background-position-x: $space_x_800 + $i * 22;
    }
  }

  @for $i from 0 through 14 {
    $space_x: 220;
    $width_rem: 1200;
    @media screen and (min-height: 800px) and (min-width: rem($width_rem + ($i * 50))) {
      background-size: rem(1570);
      background-position-x: rem($space_x + ($i * 33));
    }
  }

  @for $i from 0 through 14 {
    $width_1920: 1920px;
    $space_x_1920: 530;
    @media screen and (min-height: 800px) and (min-width: $width_1920 + $i * 50) {
      background-position-x: rem($space_x_1920 + ($i * 33));
    }
  }

  @for $i from 0 through 14 {
    $space_x_1: 330;
    $width: 1200px;
    @media screen and (max-height: 800px) and (min-width: $width + $i * 50) {
      background-size: rem(1700);
      background-position-x: rem($space_x_1 + ($i * 35));
    }
  }

  @for $i from 0 through 24 {
    $space_x_4k: 450;
    $width_4k: 3940px;
    @media screen and (min-height: 800px) and (min-width: $width_4k + $i * 50) {
      background-size: rem(1700);
      background-position-x: rem($space_x_4k + ($i * 11));
    }
  }
}

