#support {
  @include lg-block {
 
  }
  @media screen and (min-width: 992px) and (max-height: 800px) {
    height: 100vh;
  }

}
.support {
  p {
    margin: rem(16) 0;
  }
  @include xs-block {
    background: none; 
  }
  @include lg-block {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: no-repeat url("../img/svg/support_bg_lg.svg");
    background-size: rem(450) rem(1200);
    background-position: 100% rem(-270);
    margin-top: rem(120);
    position: relative;
    min-height: rem(700);
  }
  @media screen and (min-width: 992px) and (max-height: 800px) {
    height: calc(100vh - 18.93rem);
  }
  @include xl-block {
    height: calc(100vh - 18.93rem);
    margin-top: rem(120);
    background-position: 100% rem(-200);
    background-size: rem(550) rem(1200);
  }

    .title {
        font-size: rem(48);
        @include xs-block {
          margin-top: 0;
        }
        @include lg-block {
          line-height: 100px;
        }
      }

      .support_mob_title {
        @include lg-block {
          display: none;
        }
      }
      .support_web_title {
        @include xs-block {
          display: none;
        }
        @include lg-block {
          display: inherit;
        }
      }

    .content {
      display: flex;
      padding-bottom: rem(10);
      @include xs-block {
        flex-direction: column;
      }
      @include lg-block {
        flex-direction: row;
        justify-content: space-between;
      }
      @include xl-block {
        flex-direction: row;
        justify-content: space-between;
      
      }
    
        .description {
          @include xs-block {
            width: 100%;
          }
          @include lg-block {
            width: rem(450);
          }
          @include xl-block {
            width: rem(480)
          }
            .text {
              color: #000;
              font-weight: 300;

              @include xs-block {
                text-align: left;
                margin-bottom: 120px;
              }

              @include lg-block {
                font-size: rem(24);
                line-height: rem(28);
                margin-bottom: 0;
              }
              @include xl-block {
                font-size: rem(26);
                line-height: rem(30);
              }
                a {
                  color: #0072FF;
                  &:hover {
                      text-decoration-line: underline;
                  }
              }
            }
          }
      
          .viwe {
            @include xs-block {
              margin:0;
              margin-bottom: rem(60);
              width: 100%;
            }
            @include lg-block {
               width: rem(450);
             }
            @include xl-block {
             width: rem(540);
           }

           .bg_smoll {
            position: absolute;
            background: #F2F2F2;
            background-size:  100%;
            width: 100vw;
            height: rem(480);
            z-index: -1;
            left: 0%;
            
            @include xs-block {
              top: 540px;
            }

            @include sm-block {
              top: 577px;
            }
            @include md-block {
              top: 515px;
            }

            @include lg-block {
              display: none;
            
            }
           }

            .suport_list {
              display: flex;
              flex-direction: column;
              align-items: center;
              position: relative;
              @include lg-block {
                margin-top: rem(159);
              }
              
            //  .bg_lg {

            //    @include lg-block {
            //      position: absolute;
            //      z-index: -1;
            //      width: rem(450);
            //      height: 77vh;
            //      top:rem(-100);
            //      left: 0;
            //      border-radius: rem(60) rem(60) 0 0;
            //      background: #F2F2F2;
            //    }
            //    @include xl-block {
            //     z-index: -1;
            //     height: 81vh;
            //     width: rem(500);
            //     top:rem(-130);
            //     left: rem(19);
            //     border-radius: rem(80) rem(80) 0 0;

            //   }
            //  }

              .item {
                box-sizing: border-box;
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: rem(100);
                border-radius: rem(28);
                margin-bottom: rem(30);
                padding-left: rem(25);
                background: no-repeat url("../img/svg/Suport_link_arr.svg"), #FFFFFF;

                @include xs-block {
                  background-position: 285px center;
                  width: 100%;
                }
                @include sm-block {
                  background-position: 380px center;
                  width: 420px;
                }
                @include lg-block {
                  background-position: rem(340) center;
                  width: rem(380);
                }
                @include xl-block {
                  background-position: rem(380) center;
                  width: rem(420);
                }

                h4 {
                    margin-bottom: rem(10);
                }
                p {
                  margin: 0;
                  font-size: rem(14);
                  line-height: rem(22);
                  color: #9297A2;
                  font-size: 300;
                }
              }
        }
      }
    }
    .footer{
      @include lg-block {
        position: absolute;
        bottom: 0;
      }
      .footer_xl {
        position: static;
        
        .footer__navigation{
          margin-top: 0;
          .footer__socials {
            @include lg-block {
              display: none;
            }
          }
        }
      }
    }
}